<template>
    <div class="container">
        <h1>test</h1>
        <h3>{{RoomId}} | {{RoomData.num}}</h3>
        <p v-for="player in PlayerData"  :key="player.id">{{player.id}}</p>
    <b-button @click="back()" class="btn btn-info mt-3">返回</b-button>

    </div>
</template>

<script>
import router from '@/router'
import { db } from '@/db'

const RoomPromise = {
  GetRoom (id) {
    return new Promise((resolve, reject) => {
      db.doc(id).get().then(doc => resolve(doc.data()))
    })
  },
  GetRoomPlayer (id) {
    return new Promise((resolve, reject) => {
      db.doc(id).collection('player').onSnapshot(docs => {
        const items = []
        let i = 1
        docs.forEach(function (doc) {
          const s = doc.data()
          s.id = i
          items.push(s)
          i++
        })
        resolve(items)
      })
    })
  }
}

export default {
  name: 'Room',
  data () {
    return {
      RoomId: '',
      RoomData: {},
      PlayerData: {}
    }
  },
  mounted () {
    this.RoomHandler()
  },
  watch: {
    $route (now) {
      this.RoomHandler()
    }
  },
  methods: {
    RoomHandler: function () {
      const loading = this.$loading({
        lock: true,
        background: 'rgba(255, 255, 255, 0.7)'
      })
      const RoomId = this.$route.params.RoomId
      this.RoomId = RoomId
      RoomPromise.GetRoom(RoomId).then(success => {
        if (!success) router.replace({ path: '/joinroom' })
        this.RoomData = success
        loading.close()
        return RoomPromise.GetRoomPlayer(RoomId)
      }).then(data => { this.PlayerData = data })
    },
    back: function () {
      router.replace({ path: '/joinroom' })
    }
  }
}
</script>
